@tailwind base;
@tailwind components;
@tailwind utilities;

.container {
  max-width: 1450px;
  padding: 0 15px;
  margin: 0 auto;
}

/* header */

.header__ul {
  display: flex;
}

.head__logo {
  width: 50px;
  height: 50px;
}

.header__title {
}

/* HOME  */

.home {
  background-image: url("https://i.etsystatic.com/36627193/r/il/76cc50/5211422933/il_fullxfull.5211422933_tkpt.jpg");
  background-size: cover; /* Масштабирование изображения для покрытия всего элемента */
  background-repeat: no-repeat; /* Отключить повторение изображения */
  background-position: center center; /* Позиционирование изображения в центре */
  width: 100%; /* Устанавливает ширину элемента */
  height: 100vh; /* Устанавливает высоту элемента на 100% от высоты окна браузера */
}

.home__timer{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
